.home-container {
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.home-item {
    margin: 0 auto;
    padding: 10px;
}
.home-item > a {
    font-size: 42px;
}
.test-home {
    padding-top: 30px;
    text-align: center;
    font-size: 30px;
}
.contents {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    text-align: center;
}
.message {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    color: red;
    font-size: 30px;
}
.start-message {
    padding: 0 20px;
}
.start-btn {
    background: #d93434;
    background-image: -webkit-linear-gradient(top, #d93434, #b82b2b);
    background-image: linear-gradient(to bottom, #d93434, #b82b2b);
    border-radius: 10px;
    color: #ffffff;
    font-size: 14px;
    padding: 5px 10px 5px 10px;
    text-decoration: none;
  }
  
  .start-btn:hover {
    cursor: pointer;
    background: #fc3c3c;
    background-image: -webkit-linear-gradient(top, #fc3c3c, #d93434);
    background-image: linear-gradient(to bottom, #fc3c3c, #d93434);
    text-decoration: none;
  }
.table {
    margin: 0 auto;
    cursor: pointer;
    text-align: center;
    font-size: 60px;
    display: flex;
    flex-wrap: wrap;
    width: 600px;
}
.cell {
    padding: 10px 5px;
    width: 100px;
    max-width: calc(20% - 12px);
    text-align: center;
    border: 1px solid black;
}
.cell:active {
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
}

.info__error {
    color: red;
    -webkit-animation: blink 1s infinite;
            animation: blink 1s infinite;
}

@-webkit-keyframes blink{
    0%{opacity: 0.1;}
    100%{opacity: 1;}
    }

@keyframes blink{
    0%{opacity: 0.1;}
    100%{opacity: 1;}
    }
.nav-wrapper {
    background-color: #f7f3ec;
}
.inner-wrapper {
    padding: 10px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.nav-right {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.nav-right > li{
    list-style-type: none;
    padding-left: 20px;
}
.logout {
    cursor: pointer;
    text-decoration: underline;
}
* {
    margin: 0
}
.container {
    margin: 0 auto;
    width: 1080px;
}
.next {
    margin: 0 auto;
    width: 100px;
}
